


@keyframes bob {
    0% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-3vw);
    }
    100% {
        transform: translateY(0);
    }
}


@keyframes grow {
    0% {
      transform: scale(0);
    }
    100% {
        transform: scale(1);
    } 
}

@keyframes zip {
  0% {
    transform: translateX(-30vw);
   
  }
  100% {
      transform: translateX(0);
  } 
}
@keyframes zipy {
  0% {
    transform: translateX(-50vw);
   
  }
  100% {
      transform: translateX(0);
  } 
}

